@import url("https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&family=Merienda:wght@400;700&display=swap");

.Log {
  width: 30%;
  height: 30%;
  background-color: rgba($color: #ad652a, $alpha: 1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  border-radius: 10px;
  top: 30%;
  left: 33%;

  span {
    color: whitesmoke;
    font-size: 1.3em;
    font-family: "Merienda", cursive;
  }

  input {
    min-width: 40%;
    padding: 5px;
  }

  button {
    background-color: royalblue;
    text-transform: uppercase;
    font-size: 1.1em;
    color: seashell;
    font-family: "Merienda", cursive;
    border-radius: 7px;
  }
}
.game-mode {
  max-width: 100%;
  height: 100vh;
  position: relative;

  .game-field {
    border: rgba($color: #2b1b02, $alpha: 1) 9px inset;
    width: 45%;
    height: 70%;
    background-image: url("../../assets/images/table-cover.jpg");
    background-position: center;
    background-size: cover;
    border-radius: 3% 3% 50% 50%;
    position: relative;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -60%);
    display: flex;
    align-items: flex-end;
    justify-content: space-around;

    img {
      position: absolute;
      top: 5%;
      left: 5%;
    }

    .card-place {
      border: whitesmoke 1px solid;
      width: 9.5%;
      height: 15%;
      margin-bottom: 30%;
      border-radius: 2%;
    }
    .card-place:nth-child(1) {
      transform: rotate(70deg);
    }
    .card-place:nth-child(2) {
      margin-bottom: 5%;
    }
    .card-place:nth-child(3) {
      transform: rotate(-70deg);
    }
  }

  button {
    width: 8%;
    height: 6%;
    background-color: rgb(35, 63, 155);
    text-transform: uppercase;
    font-size: 1.4em;
    position: absolute;
    bottom: 10%;
    color: whitesmoke;
    border-radius: 35%;
  }

  button[disabled] {
    background: rgb(99, 97, 97);
  }

  button:nth-child(2) {
    right: 20%;
  }
  button:nth-child(3) {
    right: 10%;
  }

  .player-hand {
    position: absolute;
    top: 0;
  }
}
