@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&family=Merienda:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&family=Merienda:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&family=Merienda:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MainMenu{max-width:100%;height:100vh;position:relative}.MainMenu .menu{width:50%;height:60%;background-image:url(/static/media/menu-bg.e8db4323.jpg);background-position:center;border-radius:40px;position:absolute;top:50%;left:50%;transform:translate(-50%, -60%);display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}.MainMenu .menu button{font-family:"Merienda", cursive;font-size:1.55em;min-width:30%;height:12%;background:#2f640c;border-radius:35px;color:#ffd9f9}.MainMenu .menu button:hover{background:#00a71c}

.MenuBtn{position:absolute;left:3%;top:3%;font-family:"Akaya Kanadaka", cursive;font-size:1.1em;border-radius:5px;background:linear-gradient(#26361d, #420d18);color:whitesmoke;z-index:2}

.Sound{right:5%;top:3%;position:absolute;font-family:"Akaya Kanadaka", cursive;font-size:1.1em;border-radius:5px;background:linear-gradient(#232f44, #381219);color:whitesmoke;z-index:2}

.Log{width:30%;height:30%;background-color:#ad652a;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;position:absolute;border-radius:10px;top:30%;left:33%}.Log span{color:whitesmoke;font-size:1.3em;font-family:"Merienda", cursive}.Log input{min-width:40%;padding:5px}.Log button{background-color:royalblue;text-transform:uppercase;font-size:1.1em;color:seashell;font-family:"Merienda", cursive;border-radius:7px}.game-mode{max-width:100%;height:100vh;position:relative}.game-mode .game-field{border:#2b1b02 9px inset;width:45%;height:70%;background-image:url(/static/media/table-cover.78bc963d.jpg);background-position:center;background-size:cover;border-radius:3% 3% 50% 50%;position:relative;top:60%;left:50%;transform:translate(-50%, -60%);display:flex;align-items:flex-end;justify-content:space-around}.game-mode .game-field img{position:absolute;top:5%;left:5%}.game-mode .game-field .card-place{border:whitesmoke 1px solid;width:9.5%;height:15%;margin-bottom:30%;border-radius:2%}.game-mode .game-field .card-place:nth-child(1){transform:rotate(70deg)}.game-mode .game-field .card-place:nth-child(2){margin-bottom:5%}.game-mode .game-field .card-place:nth-child(3){transform:rotate(-70deg)}.game-mode button{width:8%;height:6%;background-color:#233f9b;text-transform:uppercase;font-size:1.4em;position:absolute;bottom:10%;color:whitesmoke;border-radius:35%}.game-mode button[disabled]{background:#636161}.game-mode button:nth-child(2){right:20%}.game-mode button:nth-child(3){right:10%}.game-mode .player-hand{position:absolute;top:0}

#root{background-image:url(/static/media/background.41eb82c9.png);background-position:center;background-size:cover;max-width:100%;height:100vh}button:hover{transform:scale(1.1);transition:transform 0.2s ease-in-out}

